"use client"

import { Transaction } from "@/lib/types"
import { ArrowUpIcon, ArrowDownIcon, Trash2Icon, Edit2Icon } from 'lucide-react'
import { Button } from "@/components/ui/button"
import { toast } from "sonner"
import { useState } from "react"

interface TransactionItemProps {
  transaction: Transaction
}

export function TransactionItem({
  transaction,
}: TransactionItemProps) {
  const [isDeleted, setIsDeleted] = useState(false)
  
  const isIncome = transaction.type === "income"
  const date = new Date(transaction.date)
  const formattedDate = date.toLocaleDateString("en-US", {
    month: "short",
    day: "numeric",
  })

  const handleEdit = () => {
    toast.info('Edit functionality would open a modal or form')
  }

  const handleDelete = async () => {
    try {
      const response = await fetch(`/api/transactions/delete/${transaction.id}`, {
        method: 'DELETE',
      })
      
      const result = await response.json()
      
      if (!response.ok || !result.success) {
        toast.error(result.error || 'Failed to delete transaction')
        return
      }
      
      toast.success('Transaction deleted successfully')
      setIsDeleted(true)
    } catch (error) {
      toast.error('An unexpected error occurred')
    }
  }

  if (isDeleted) {
    return null
  }

  return (
    <div className="flex items-center justify-between rounded-lg border p-4 hover:bg-muted/50 transition-colors">
      <div className="flex items-center gap-4">
        <div
          className={`flex h-10 w-10 items-center justify-center rounded-full ${
            isIncome ? "bg-green-100 text-green-600" : "bg-red-100 text-red-600"
          }`}
        >
          {isIncome ? (
            <ArrowUpIcon className="h-5 w-5" />
          ) : (
            <ArrowDownIcon className="h-5 w-5" />
          )}
        </div>
        <div>
          <p className="font-medium">{transaction.reason}</p>
          <p className="text-sm text-muted-foreground">
            {transaction.category} â€¢ {formattedDate}
          </p>
        </div>
      </div>
      <div className="flex items-center gap-4">
        <p
          className={`text-lg font-semibold ${
            isIncome ? "text-green-600" : "text-red-600"
          }`}
        >
          {isIncome ? "+" : "-"}${transaction.amount.toLocaleString()}
        </p>
        <div className="flex gap-2">
          <Button
            variant="ghost"
            size="icon"
            onClick={handleEdit}
          >
            <Edit2Icon className="h-4 w-4" />
          </Button>
          <Button
            variant="ghost"
            size="icon"
            onClick={handleDelete}
          >
            <Trash2Icon className="h-4 w-4" />
          </Button>
        </div>
      </div>
    </div>
  )
}